function ServiceCardComponent({ service }: { service: typeof services[0] }) {
  return (
    <ServiceCard to={service.to}>
      <IconImage src={service.iconUrl} alt={service.title} />
      <ServiceTitle>{service.title}</ServiceTitle>
    </ServiceCard>
  );
}

export default function HomePage() {
  const navigate = useNavigate();
  const { contacts, addContact } = useContactsStore();
  const { isDarkMode, toggleTheme } = useThemeStore();
  const [showModal, setShowModal] = useState(false);
  const [name, setName] = useState('');
  const [messenger, setMessenger] = useState<string>('');

  const handleSubmit = () => {
    if (!name.trim() || !messenger) return;

    const newContact = {
      id: uuidv4(),
      name: name.trim(),
      messenger: messenger as 'kakao' | 'instagram' | 'telegram' | 'facebook',
      createdAt: Date.now(),
    };

    addContact(newContact);
    setShowModal(false);
    setName('');
    setMessenger('');
    navigate({ to: '/analyze/$id', params: { id: newContact.id } });
  };

  const getMessenger = (id: string) => messengers.find(m => m.id === id);

  return (
    <Container>
      <Nav>
        <NavInner>
          <Logo>
            <LogoIcon>
              <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="#fff" strokeWidth="2.5">
                <path d="M12 22s8-4 8-10V5l-8-3-8 3v7c0 6 8 10 8 10z"/>
              </svg>
            </LogoIcon>
            Enigma
          </Logo>
          <NavActions>
            <ThemeToggle onClick={toggleTheme} aria-label="Toggle theme">
              {isDarkMode ? <SunIcon /> : <MoonIcon />}
            </ThemeToggle>
            <NavButton onClick={() => setShowModal(true)}>
              분석 시작
            </NavButton>
          </NavActions>
        </NavInner>
      </Nav>

      <HeroSection>
        <HeroBadge>
          <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="#635bff" strokeWidth="2">
            <path d="M12 22s8-4 8-10V5l-8-3-8 3v7c0 6 8 10 8 10z"/>
          </svg>
          <BadgeText>AI 기반 스캠 탐지 서비스</BadgeText>
        </HeroBadge>
        <HeroTitle>
          로맨스 스캠으로부터<br />
          당신을 보호합니다
        </HeroTitle>
        <HeroDesc>
          Enigma는 AI 기술을 활용하여 대화, 프로필, 미디어를 분석하고
          로맨스 스캠의 위험 신호를 탐지하여 피해를 예방합니다.
        </HeroDesc>
      </HeroSection>

      <ServiceGrid>
        {services.map((service) => (
          <ServiceCardComponent key={service.to} service={service} />
        ))}
      </ServiceGrid>

      {contacts.length > 0 && (
        <ContactsSection>
          <SectionHeader>
            <SectionLabel>진행 중</SectionLabel>
            <SectionTitle>분석 중인 대상</SectionTitle>
          </SectionHeader>
          <ContactsList>
            {contacts.map(contact => {
              const m = getMessenger(contact.messenger);
              return (
                <ContactCard key={contact.id} to={`/analyze/${contact.id}`}>
                  <ContactAvatar>
                    {m && <m.Icon />}
                  </ContactAvatar>
                  <ContactInfo>
                    <ContactName>{contact.name}</ContactName>
                    <ContactMeta>{m?.name}</ContactMeta>
                  </ContactInfo>
                  <ArrowIcon />
                </ContactCard>
              );
            })}
          </ContactsList>
        </ContactsSection>
      )}

      <Modal
        isOpen={showModal}
        onClose={() => setShowModal(false)}
        title="새 분석 대상 추가"
      >
        <InputLabel>상대방 이름 (닉네임)</InputLabel>
        <Input
          value={name}
          onChange={e => setName(e.target.value)}
          placeholder="예: John, 제임스"
        />

        <InputLabel>사용 중인 메신저</InputLabel>
        <MessengerGrid>
          {messengers.map(m => (
            <MessengerBtn
              key={m.id}
              $active={messenger === m.id}
              onClick={() => setMessenger(m.id)}
            >
              <m.Icon />
            </MessengerBtn>
          ))}
        </MessengerGrid>

        <SubmitBtn
          onClick={handleSubmit}
          disabled={!name.trim() || !messenger}
        >
          분석 시작하기
        </SubmitBtn>
      </Modal>
    </Container>
  );
}
